.auth{
    margin-top: 60px;
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: space-evenly;
    margin-bottom: 84px;
}

.signup{
    width: 450px;
    background-color: white;
    border-radius: 25px;
    display: flex;
    flex-direction: column;
}

.login{
    width: 400px;
    background-color: white;
    border-radius: 25px;
    display: flex;
    flex-direction: column;
}

.username{
    margin-left: 20px;
    margin-right: 20px;
    margin-top: 10px;
    margin-bottom: 10px;
    /* background-color: #d6d6d6; */
    /* border-radius: 25px; */
    padding-top: 8px;
    padding-bottom: 8px;
    display: flex;
    align-items: center;
    justify-content: space-evenly;
}

.username > p{
    margin-right: 10px;
    margin-left: 20px;
}

.button{
    display: flex;
    align-items: center;
}

.signup >h4{
    text-align: center;
    width: max-content;
    margin-left: auto;
    margin-right: auto;
    margin-bottom: 15px;
    margin-top: 10px;
    padding: 6px 10px 6px 10px;
    transition: 0.2s;
    background-color: #d6d6d6;
    border-radius: 25px;
}


.signup >h4:hover{
    cursor: pointer;
    background-color: #101010;
    color: white;
}

.login >h4{
    text-align: center;
    width: max-content;
    margin-left: auto;
    margin-right: auto;
    margin-bottom: 15px;
    margin-top: 10px;
    padding: 6px 10px 6px 10px;
    transition: 0.2s;
    background-color: #d6d6d6;
    border-radius: 25px;
}


.login >h4:hover{
    cursor: pointer;
    background-color: #101010;
    color: white;
}

#filled-basic{
    width: 350px;
    height: 10px;
}