.mainright{
    width: 300px;
    height: 700px;
    margin-left: 30px;
    align-items: center;
}

h3{
    margin-top: 30px;
    margin-bottom: 10px;
}

.mainright > img{
    object-fit: contain;
    width: 250px;
    margin-left: auto;
    margin-right: auto;
    margin-top: 50px;
    display: block;
}

.support{
    background-color: white;
    height: 255px;
    border-radius: 25px;
    display: flex;
    align-items: center;
}

.support__content{
    align-items: center;
    margin-left: auto;
    margin-right: auto;
}