.center__intro{
    background-color: white;
    width: 700px;
    height: max-content;
    border-radius: 25px;
    align-items: center;
    margin-left: auto;
    margin-right: auto;
    margin-top: 60px;
    padding-top: 40px;
    padding-bottom: 40px;
}

.center__heading{
    width: max-content;
    margin-left: auto;
    margin-right: auto;
    padding: 5px;
}

.center__heading > h1{
    font-weight: bold;
}

.center__info{
    width: max-content;
    margin-left: auto;
    margin-right: auto;
    padding: 5px;
}