.header{
    display: flex;
    justify-content: space-between;
    height: 60px;
    align-items: center;
    background-color: white;
    border-bottom-left-radius: 25px;
    border-bottom-right-radius: 25px;
}

.header__right{
    display: flex;
    margin-right: 60px;
}

.header__right >h4{
    margin-left: 10px;
    padding: 6px 10px 6px 10px;
    transition: 0.2s;
}


.header__right >h4:hover{
    cursor: pointer;
    background-color: #d6d6d6;
    border-radius: 25px;
}


.header__logo{
    margin-left: 60px;
}

.header__logo > img{
    object-fit: contain;
    width: 200px;
}