.App{
    align-items: center;
}

.input__blog{
    display: flex;
    background-color: white;
    width: 500px;
    padding: 8px;
    border-radius: 25px;
    margin-top: 50px;
    margin-left: auto;
    margin-right: auto;
}

.input__blog > input{
    flex: 1;
    outline: none;
    border: none;
}

.input__blog > h4{
    padding: 4px 8px 4px 8px;
    transition: 0.2s;
    border-left: 1px solid #d6d6d6;
    font-weight: 500;
}

.input__blog > h4:hover{
    cursor: pointer;
    background-color: #d6d6d6;
    border-radius: 25px;
}

.main__content{
    margin-top: 60px;
    width: max-content;
    display: flex;
    margin-left: auto;
    margin-right: auto;
}